<script>
import $ from 'jquery'
import {mapState} from 'vuex'
export default {
    name: 'Display',
    data(){
        return {
            color: this.$store.state.color,

            bkrotation: 0,
            picrotation: 0,
            shaprotation:0,
            txtrotation:0,

            bkscale: 1,
            picscale: 1,
            shapscale: 1,
            txtscale: 1,

            bkopacity: 1,
            picopacity: 1,
            shapopacity: 1,
            txtopacity: 1,

            bckels: document.getElementsByClassName("back manipulated"),
            txtels: document.getElementsByClassName("txt manipulated"),
            picels: document.getElementsByClassName("pic manipulated"),
            shapels: document.getElementsByClassName("shap manipulated"),
        }
    },
    methods: {
        // image events

        //rotation
        rotate() {
            if($(".on").hasClass("back")){
                this.bkrotation += 20;
                $(".on").css({ "transform": `rotate(${this.bkrotation}deg) scale(${this.bkscale})` });
            }
            else if($(".on").hasClass("pic")){
                this.picrotation += 20;
                $(".on").css({ "transform": `rotate(${this.picrotation}deg) scale(${this.picscale})` });
            }
            else if($(".on").hasClass("shap")){
                this.shaprotation += 20;
                $(".on").css({ "transform": `rotate(${this.shaprotation}deg) scale(${this.shapscale})` });
            }
            else if($(".on").hasClass("txt")){
                this.txtrotation += 20;
                $(".on").css({ "transform": `rotate(${this.txtrotation}deg) scale(${this.txtscale})` });
            }
        },
        //scale
        zoomin(){
            if($(".on").hasClass("back")){
                this.bkscale += .3;
                $(".on").css({"transform":`scale(${this.bkscale}) rotate(${this.bkrotation}deg)`});
            }
            else if($(".on").hasClass("pic")){
                this.picscale += .3;
                $(".on").css({"transform":`scale(${this.picscale}) rotate(${this.picrotation}deg)`});
            }
            else if($(".on").hasClass("shap")){
                this.shapscale += .3;
                $(".on").css({"transform":`scale(${this.shapscale}) rotate(${this.shaprotation}deg)`});
            }
            else if($(".on").hasClass("txt")){
                this.txtscale += .3;
                $(".on").css({"transform":`scale(${this.txtscale}) rotate(${this.txtrotation}deg)`});
            }
        },
        zoomout(){
            if($(".on").hasClass("back")){
                this.bkscale -= .3;
                $(".on").css({"transform":`scale(${this.bkscale}) rotate(${this.bkrotation}deg)`});
            }
            else if($(".on").hasClass("pic")){
                this.picscale -= .3;
                $(".on").css({"transform":`scale(${this.picscale}) rotate(${this.picrotation}deg)`});
            }
            else if($(".on").hasClass("shap")){
                this.shapscale -= .3;
                $(".on").css({"transform":`scale(${this.shapscale}) rotate(${this.shaprotation}deg)`});
            }
            else if($(".on").hasClass("txt")){
                this.txtscale -= .3;
                $(".on").css({"transform":`scale(${this.txtscale}) rotate(${this.txtrotation}deg)`});
            }
        },
        //opacity
        fadein(){
            if($(".on").hasClass("back")){
                this.bkopacity += .2;
                $(".on").css({"transform":`scale(${this.bkscale}) rotate(${this.bkrotation}deg)`, "opacity":`${this.bkopacity}`});
            }
            else if($(".on").hasClass("pic")){
                this.picopacity += .2;
                $(".on").css({"transform":`scale(${this.picscale}) rotate(${this.picrotation}deg)`, "opacity":`${this.picopacity}`});
            }
            else if($(".on").hasClass("shap")){
                this.shapopacity += .2;
                $(".on").css({"transform":`scale(${this.shapscale}) rotate(${this.shaprotation}deg)`, "opacity":`${this.shapopacity}`});
            }
            else if($(".on").hasClass("txt")){
                this.txtopacity += .2;
                $(".on").css({"transform":`scale(${this.txtscale}) rotate(${this.txtrotation}deg)`, "opacity":`${this.txtopacity}`});
            }
        },

        fadeout(){

            if($(".on").hasClass("back")){
                this.bkopacity -= .2;
                $(".on").css({"transform":`scale(${this.bkscale}) rotate(${this.bkrotation}deg)`, "opacity":`${this.bkopacity}`});
            }
            else if($(".on").hasClass("pic")){
                this.picopacity -= .2;
                $(".on").css({"transform":`scale(${this.picscale}) rotate(${this.picrotation}deg)`, "opacity":`${this.picopacity}`});
            }
            else if($(".on").hasClass("shap")){
                this.shapopacity -= .2;
                $(".on").css({"transform":`scale(${this.shapscale}) rotate(${this.shaprotation}deg)`, "opacity":`${this.shapopacity}`});
            }
            else if($(".on").hasClass("txt")){
                this.txtopacity -= .2;
                $(".on").css({"transform":`scale(${this.txtscale}) rotate(${this.txtrotation}deg)`, "opacity":`${this.txtopacity}`});
            }
        },



        //reset
        reset() {

            this.bkrotation = 0,
            this.picrotation = 0,
            this.shaprotation = 0,
            this.txtrotation = 0,

            this.bkscale = 1,
            this.picscale = 1,
            this.shapscale = 1,
            this.txtscale = 1,

            this.bkopacity = 1,
            this.picopacity = 1,
            this.shapopacity = 1,
            this.txtopacity = 1,
           
            $(".on").css({"transform":`rotate(0deg) scale(1)` , "opacity":`1`});
        }
    },
    mounted(){
        //drag & drop
        $( ".manipulated" ).draggable();

        //handle image
        $('.manipulated').on('click', function () {
            $('.manipulated').removeClass('on');// here remove class selected from all showfood
            $(this).addClass('on');// here apply selected class on clickable showfood
        });

        $('.manipulated').on('touchstart', function(e) {
            var link = $(e.target.parentNode).closest('a')  
            if(link.length > 0) {
                window.location.href = link.attr('href');
            }
        });
    },
    computed: {
        ...mapState([
            'backImgId'
        ]),

        test: function(){

            this.$store.state.backImgId
            this.$store.state.textImgId
            this.$store.state.shapImgId
            this.$store.state.picImgId
            console.log(this.$store.state.color)
            
            for(let i=0 ; i < this.bckels.length ; i++)
            {
                if(this.$store.state.backImgId == $(this.bckels[i]).attr("id"))
                {
                    $(this.bckels[i]).addClass('selected');
                    $(this.bckels[i]).removeClass('d-none');
                }
                else{
                    $(this.bckels[i]).addClass('d-none');
                    $(this.bckels[i]).removeClass('selected');
                }
            }

            for(let j=0 ; j < this.txtels.length ; j++)
            {
                if(this.$store.state.textImgId == $(this.txtels[j]).attr("id"))
                {
                    $(this.txtels[j]).addClass('selected');
                    $(this.txtels[j]).removeClass('d-none');
                }
                else{
                    $(this.txtels[j]).addClass('d-none');
                    $(this.txtels[j]).removeClass('selected');
                }
            }

            for(let x=0 ; x < this.picels.length ; x++)
            {
                if(this.$store.state.picImgId == $(this.picels[x]).attr("id"))
                {
                    $(this.picels[x]).addClass('selected');
                    $(this.picels[x]).removeClass('d-none');
                }
                else{
                    $(this.picels[x]).addClass('d-none');
                    $(this.picels[x]).removeClass('selected');
                }
            }

            for(let y=0 ; y < this.shapels.length ; y++)
            {
                if(this.$store.state.shapImgId == $(this.shapels[y]).attr("id"))
                {
                    $(this.shapels[y]).addClass('selected');
                    $(this.shapels[y]).css("fill",`${this.color}`)
                    $(this.shapels[y]).removeClass('d-none');
                }
                else{
                    $(this.shapels[y]).addClass('d-none');
                    $(this.shapels[y]).removeClass('selected');
                }
            }
            return false;
        }
    },
    watch: {
        test(){}
    }
}
</script>

<style>
.display {
    height: 100%;
}
.pad {
    height: 500px;
    width: 330px;
    border: 1px solid #ccc;
    position: relative;
    overflow: hidden;
}
.pad-title{
    max-width: 480px;
    margin: 0 auto;
}  
.icon{
    cursor: pointer;
}

img {
    width: 70%;
    cursor: pointer;
}

.manipulated {
    position: absolute;
}

.on {
    border: blue 2px solid;
}

@media (max-width: 768px) {
    .icon{
        padding: 0px 5px;
    }
}
</style>

